.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro{
    max-width: 540px;
    text-align: center;
    z-index: 1;
}

.home__img{
    margin-bottom: 1.5rem;
}
.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}
.home__socials{
    display: flex;
    justify-content: space-evenly;
    column-gap: 1.75rem;
    margin: 1.5rem 0;
}
.home__social-link{
    color: var(--title-color);
    font-size: 1.3rem;
    transition: .3s;
}
.home__social-link:hover{
    color: hsl(43,100%,68%);
}
.scroll__down{
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}
.home__scroll-name{
    font-size: var(--small-font-size);
}
.mouse{
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}
@keyframes ani-mouse {
    0%{
        top: 29%;
    }
    15%{
        top: 50%;
    }
    50%{
        top: 50%;
    }
    100%{
        top: 29%;
    }
}
.wheel{
    background-color: var(--title-color);
    border-radius:100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}
.shape{
    position: absolute;
}
.s1{
    left: 2%;
    top:10%;
}
.s2{
    left: 18%;
    top:30%;
}
.s3{
    left: 5%;
    top:30%;
}
.s4{
    left: 2%;
    top:10%;
}
.s5{
    left: 44%;
    top:10%;
}
.s6{
    left:36%;
    top:10%;
}
.s7{
    left:20%;
    top:25%;
}
.s8{
    left:24%;
    top:20%;
}
.s9{
    left: 2%;
    top:10%;
}
.s10{
    left:45%;
    top:11%;
}
.s11{
    left:10%;
    top:2%;
}
.home__img{
    width: 235px;
    height: 250px;
    object-fit: cover;
    border-radius: 50%;
}
@media screen and (max-width:1024px){
    .home__social-link{
        font-size: 3.5rem;
    }
    .home__img{
        margin-bottom: 5.5rem;
        width: 15rem;
    }
    .home__name{
        font-size: 5rem;
    }
    .home__education{
        font-size: 3rem;
    }
  }